/*
 * componet tab 
 * 
 *  labels  
 *  tabkey  
 * 
 *  tabs class:tab-cont_item
 * eg:
 * <tabs :labels="['1','2']" :tabkey="0">
 *  <div>1</div>
 *  <div>2</div>
 * </tabs>
 */

import Tabs from './src/main'

Tabs.install = function (Vue) {
  Vue.component(Tabs.name, Tabs)
}

export default Tabs



